import React, { Component } from "react";
import "./App.css";
import { Layout, Header, Navigation, Drawer, Content } from "react-mdl";
import Main from "./components/main";
import { Link} from "react-router-dom"

class App extends Component {

  hideToggle() {
    var selectorId = document.querySelector('.mdl-layout');
    selectorId.MaterialLayout.toggleDrawer();
  }

  render() {
    return (
      <div style={{ height: "300px", position: "auto" }}>
        <Layout fixedHeader>
          <Header
            title={
              <span>
                <span style={{ color: "#ddd" }}>LZBFGAZ/Quote Sheet</span>
              </span>
            }
          />
          <Drawer>
            <Navigation>
              <Link to="#/Camelback" onClick={() => this.hideToggle()} >
                Camelback
              </Link>
              <Link to="#/Broadway" onClick={() => this.hideToggle()}>
                Broadway
              </Link>
              <Link to="#/Ina" onClick={() => this.hideToggle()}>
                Ina
              </Link>
              <Link to="#/Bell" onClick={() => this.hideToggle()}>
                Bell
              </Link>
              <Link href="#/Chandler" onClick={() => this.hideToggle()}>
                Chandler
              </Link>
              <Link to="#/Mesa" onClick={() => this.hideToggle()}>
                Mesa
              </Link>
              <Link to="#/Scottsdale" onClick={() => this.hideToggle()}>
                Scottsdale
              </Link>
              <Link to="#/PrescottValley" onClick={() => this.hideToggle()}>
                Prescott Valley
              </Link>
              <Link to="#/Goodyear" onClick={() => this.hideToggle()}>
                Goodyear
              </Link>
              <Link to="#/" onClick={() => this.hideToggle()}>
                Back
              </Link>
            </Navigation>
          </Drawer>
          <Content>
            <div>
              <Main />
            </div>
          </Content>
        </Layout>
      </div>
    );
  }
}

export default App;
